'use client';

import React, { useState, useEffect } from 'react';
import { Dumbbell, Menu, X } from 'lucide-react';

export default function TopNav() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => setIsScrolled(window.scrollY > 10);
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  const scrollTo = (elementId: string) => {
    const el = document.getElementById(elementId);
    if (el) el.scrollIntoView({ behavior: 'smooth' });
    setIsMenuOpen(false);
  };

  const openSignup = () => {
    try { localStorage.setItem('openSignupOnLoad', '1'); } catch {}
    if (typeof window !== 'undefined') {
      window.location.hash = 'open-signup';
      (window as any).openSignupModal?.();
    }
    scrollTo('pagamento');
  };

  const menuItems = [
    { id: 'inicio', label: 'In√≠cio' },
    { id: 'graficos', label: 'Gr√°ficos' },
    { id: 'preco', label: 'Pre√ßo' },
    { id: 'contato', label: 'Contato' },
  ];

  return (
    <header 
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
        isScrolled ? 'glass-effect shadow-2xl backdrop-blur-xl' : 'bg-transparent'
      }`}
    >
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-20">
          {/* Brand */}
          <div className="flex items-center space-x-3 cursor-pointer" onClick={() => scrollTo('inicio')}>
            <div className="relative">
              <div className="w-12 h-12 gradient-bg-primary rounded-2xl flex items-center justify-center transform transition-all duration-300">
                <Dumbbell className="w-6 h-6 text-white" />
              </div>
              <div className="absolute inset-0 w-12 h-12 gradient-bg-primary rounded-2xl opacity-30 blur-lg"></div>
            </div>
            <div className="hidden sm:block">
              <h1 className="font-orbitron text-2xl font-bold text-gray-900">
                RICARDO <span className="gradient-text">TEAM</span>
              </h1>
              <p className="text-xs text-gray-600 font-medium tracking-wider">PERSONAL TRAINER</p>
            </div>
          </div>

          {/* Desktop Nav */}
          <nav className="hidden lg:flex items-center space-x-1">
            {menuItems.map((item) => (
              <button
                key={item.id}
                onClick={() => scrollTo(item.id)}
                className="px-4 py-2 rounded-xl text-gray-700 hover:text-gray-900 hover:bg-gray-100 transition-colors"
              >
                {item.label}
              </button>
            ))}
            <button
              onClick={openSignup}
              className="ml-2 btn-primary px-6 py-3"
            >
              Contratar
            </button>
          </nav>

          {/* Mobile Toggle */}
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="lg:hidden relative w-12 h-12 rounded-2xl glass-effect flex items-center justify-center transition-all duration-300"
            aria-label="Abrir menu"
          >
            <div className="relative w-6 h-6">
              <Menu className={`absolute inset-0 w-6 h-6 text-gray-700 transition-all duration-300 ${isMenuOpen ? 'opacity-0 rotate-180' : 'opacity-100 rotate-0'}`} />
              <X className={`absolute inset-0 w-6 h-6 text-gray-700 transition-all duration-300 ${isMenuOpen ? 'opacity-100 rotate-0' : 'opacity-0 -rotate-180'}`} />
            </div>
          </button>
        </div>

        {/* Mobile Nav */}
        <div className={`lg:hidden overflow-hidden transition-all duration-500 ${isMenuOpen ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'}`}>
          <nav className="py-6 space-y-2">
            {menuItems.map((item) => (
              <button
                key={item.id}
                onClick={() => scrollTo(item.id)}
                className="w-full text-left px-6 py-4 rounded-2xl font-medium transition-all duration-300 hover:bg-gray-100"
              >
                {item.label}
              </button>
            ))}
            <button onClick={openSignup} className="w-full btn-primary px-6 py-4 mt-2">
              Contratar
            </button>
          </nav>
        </div>
      </div>
    </header>
  );
}